// /home/coffee/my-keymap-viewer/src/hooks/useYouhandakuonPractice.ts
import { useCallback, useEffect, useRef, useState } from 'react';
import {
    PracticeHookProps, // Import PracticeHookProps
    PracticeMode, PracticeHookResult, PracticeStatus, PracticeInputInfo, // Changed InputInfo to PracticeInputInfo
    PracticeHighlightResult, PracticeInputResult // Changed KeyPressValidationResult to PracticeInputResult
} from './usePracticeCommons';
import {
    youhandakuonPracticeData, youhandakuonGyouChars, youhandakuonDanMapping,
    youonGyouList, // 
    danOrder, // 
} from '../data/keymapData';
import { getKeyFromKeymap } from '../utils/getKeyFromKeymap';

const defaultTargetLayerIndex = 2; // 

interface YouhandakuonPracticeProps extends PracticeHookProps {
    practice: PracticeMode; // Add practice to the props
}

export const useYouhandakuonPractice = ({
    practice, // Destructure practice
    gIdx,
    dIdx,
    isActive,
    isRandomMode,
    onAdvance,
    layers: keymap, // Rename layers to keymap for consistency with previous usage
}: YouhandakuonPracticeProps): PracticeHookResult => {
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [targetChar, setTargetChar] = useState<string | null>(null);
    const [currentTarget, setCurrentTarget] = useState<string | null>(null); // 
    const [headingChars, setHeadingChars] = useState<string[]>([]);
    const [targetLayerIndex, setTargetLayerIndex] = useState<number>(defaultTargetLayerIndex);
    const [targetLayerIndices, setTargetLayerIndices] = useState<number[]>([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]);
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null);

    const internalStageRef = useRef<'initial' | 'waitingForYouonKey' | 'waitingForFirstDakuonKey' | 'waitingForSecondDakuonKey' | 'waitingForDanKey' | 'stageCompleted'>('initial');
    const selectedGyouKeyRef = useRef<string | null>(null);
    const selectedYouonKeyRef = useRef<string | null>(null);
    const selectedFirstDakuonKeyRef = useRef<string | null>(null);
    const selectedSecondDakuonKeyRef = useRef<string | null>(null);
    const selectedDanKeyRef = useRef<string | null>(null);
    const currentExpectedKeysRef = useRef<string[]>([]);
    const showHighlightForSecondDakuonRef = useRef<boolean>(false); // 2
    const [isBlinking, setIsBlinking] = useState(false); // 
    const blinkTimeoutRef = useRef<number | null>(null); // 

    const prevIsActiveRef = useRef(isActive);
    const prevIsRandomModeRef = useRef(isRandomMode);
    const prevGIdxRef = useRef(gIdx);
    const prevDIdxRef = useRef(dIdx);
    const isInitialMountRef = useRef(true); // 

    const getTargetInfo = useCallback(() => {
        // For Youhandakuon, gIdx from props is effectively 0 as there's only one group in youhandakuonPracticeData.
        // We directly use the first (and only) group.
        const practiceGroup = youhandakuonPracticeData[0];
        if (!practiceGroup || dIdx < 0 || dIdx >= practiceGroup.inputs.length) {
            console.warn(`[Youhandakuon getTargetInfo] Invalid dIdx: ${dIdx} for group ${practiceGroup?.groupName}`);
            return null;
        }

        const inputDef = practiceGroup.inputs[dIdx];
        const char = practiceGroup.chars[dIdx];
        const effectiveGyouName = ''; // For headingChars and danMapping lookup
        return { char, inputDef, currentGyou: effectiveGyouName };
    }, [dIdx]); // gIdx is implicitly 0 for this practice's data structure

    const isGyouKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return youonGyouList.includes(keyLabel ?? '');
    };
    const isYouonKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === '';
    };
    const isDakuonKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === '';
    };
    const isDanKey = (pressCode: number, layerIndex: number, expectedDan: string): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === expectedDan;
    };
    const isCancelKey = (pressCode: number, layerIndex: number): boolean => {
        const keyLabel = getKeyFromKeymap({ layers: keymap }, layerIndex, pressCode);
        return keyLabel === 'BS' || keyLabel === 'ENT'; // BSENT
    };

    const determineErrorLayer = (inputLayerIndex: number): number => {
        // 
        // (layer 3)
        if (internalStageRef.current === 'waitingForDanKey' && inputLayerIndex !== targetLayerIndices[1]) {
            return targetLayerIndices[1];
        }
        return inputLayerIndex;
    };


    const reset = useCallback(() => {
        // console.log('[Youhandakuon reset] Resetting practice state.');
        // getTargetInfo depends on dIdx, which might not be updated yet if reset is called
        // immediately after dIdx changes. We'll set headingChars in the useEffect.
        // For now, reset other states.
        const initialTargetInfo = getTargetInfo(); // Get initial info based on current dIdx
        if (initialTargetInfo) {
            setTargetChar(initialTargetInfo.char);
            // setHeadingChars(youhandakuonGyouChars[initialTargetInfo.currentGyou] || []); // Moved to useEffect
            setCurrentTarget(initialTargetInfo.inputDef.gyouKey); // 
            currentExpectedKeysRef.current = [initialTargetInfo.inputDef.gyouKey];
        } else {
            setTargetChar(null);
            // setHeadingChars([]); // Moved to useEffect
            setCurrentTarget(null);
            currentExpectedKeysRef.current = [];
        }
        internalStageRef.current = 'initial';
        selectedGyouKeyRef.current = null;
        selectedYouonKeyRef.current = null;
        selectedFirstDakuonKeyRef.current = null;
        selectedSecondDakuonKeyRef.current = null;
        selectedDanKeyRef.current = null;
        setStatus('waiting');
        setTargetLayerIndex(defaultTargetLayerIndex);
        setTargetLayerIndices([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]);
        setErrorHighlightLayer(null);
        showHighlightForSecondDakuonRef.current = false;
        setIsBlinking(false); // 
        if (blinkTimeoutRef.current !== null) {
            clearTimeout(blinkTimeoutRef.current);
            blinkTimeoutRef.current = null;
        }
    }, [getTargetInfo]); // Removed keymap as it's stable from props

    useEffect(() => {
        // console.log(`[Youhandakuon useEffect for props] Start. isActive=${isActive}, prevIsActive=${prevIsActiveRef.current}, isRandomMode=${isRandomMode}, prevIsRandomMode=${prevIsRandomModeRef.current}, dIdx=${dIdx}, prevDIdx=${prevDIdxRef.current}, isInitialMount=${isInitialMountRef.current}, randomTarget=${currentTarget}`);
        const gIdxChanged = gIdx !== prevGIdxRef.current;
        const dIdxChanged = dIdx !== prevDIdxRef.current;
        const activeChanged = isActive !== prevIsActiveRef.current;
        const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

        if (activeChanged && !isActive) {
            // console.log(`[Youhandakuon useEffect] Deactivated. Calling reset.`);
            reset();
            setHeadingChars([]); // Clear heading chars when inactive
        } else if (isActive) {
            // console.log(`[Youhandakuon useEffect] Active. isInitialMount=${isInitialMountRef.current}, gIdxChanged=${gIdxChanged}, dIdxChanged=${dIdxChanged}, randomModeChanged=${randomModeChanged}`);
            // This practice mode doesn't use random mode, so randomModeChanged effectively means switching to/from it.
            // It also doesn't use gIdx.
            if (isInitialMountRef.current || dIdxChanged || (activeChanged && isActive) || randomModeChanged) {
                // console.log(`[Youhandakuon useEffect] Conditions met for reset and target update.`);
                reset(); // Reset most states

                // Now that dIdx is stable (or it's the initial mount), get target info and set char/headings
                const currentTargetInfo = getTargetInfo();
                if (currentTargetInfo) {
                    // console.log(`[Youhandakuon useEffect] Setting targetChar to: ${currentTargetInfo.char}`);
                    setTargetChar(currentTargetInfo.char);
                    // console.log(`[Youhandakuon useEffect] Setting headingChars for gyou: ${currentTargetInfo.currentGyou}`);
                    setHeadingChars(youhandakuonGyouChars[currentTargetInfo.currentGyou] || []);
                    setCurrentTarget(currentTargetInfo.inputDef.gyouKey); // 
                    currentExpectedKeysRef.current = [currentTargetInfo.inputDef.gyouKey];
                } else {
                    // console.log(`[Youhandakuon useEffect] No targetInfo found. Clearing char and headings.`);
                    setTargetChar(null);
                    setHeadingChars([]);
                    setCurrentTarget(null);
                    currentExpectedKeysRef.current = [];
                }
            }
        }
        prevIsActiveRef.current = isActive;
        prevIsRandomModeRef.current = isRandomMode;
        prevGIdxRef.current = gIdx;
        prevDIdxRef.current = dIdx;
        if (isInitialMountRef.current) {
            isInitialMountRef.current = false;
        }

        // 
        return () => {
            if (blinkTimeoutRef.current !== null) {
                clearTimeout(blinkTimeoutRef.current);
            }
        };
    }, [isActive, isRandomMode, gIdx, dIdx, reset, getTargetInfo]);


    useEffect(() => {
        // 
    }, [showHighlightForSecondDakuonRef.current]); //  showHighlightForSecondDakuonRef.current 


    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (inputInfo.type !== 'release' || !isActive || internalStageRef.current === 'stageCompleted') {
            return { isExpected: false, shouldGoToNext: false, errorLayer: null }; // Return full PracticeInputResult
        }
        setErrorHighlightLayer(null); // Reset error highlight at the beginning of new input

        const targetInfo = getTargetInfo();
        if (!targetInfo) return { isExpected: false, shouldGoToNext: false, errorLayer: null };

        const { inputDef } = targetInfo;
        let layerForInputEvaluation = defaultTargetLayerIndex; // 2
        if (internalStageRef.current === 'waitingForDanKey') {
            layerForInputEvaluation = defaultTargetLayerIndex + 1; // 3
        }
        // console.log(`[Youhandakuon handleInput] Evaluating input on layer: ${layerForInputEvaluation}, App's inputInfo.layerIndex: ${inputInfo.layerIndex}`);

        console.log(`[Youhandakuon handleInput] Calling getKeyFromKeymap with layer: ${layerForInputEvaluation}, pressCode: ${inputInfo.pressCode}`);
        const inputKey = getKeyFromKeymap({ layers: keymap }, layerForInputEvaluation, inputInfo.pressCode);
        const gyouKeys = youonGyouList; // 
        const danInputs = youhandakuonDanMapping[targetInfo.currentGyou] || []; // 

        if (internalStageRef.current === 'initial') {
            if (isGyouKey(inputInfo.pressCode, layerForInputEvaluation) && inputKey === inputDef.gyouKey) {
                selectedGyouKeyRef.current = inputKey ?? null;
                internalStageRef.current = 'waitingForYouonKey';
                setCurrentTarget(''); // 
                console.log('[Youhandakuon handleInput] Stage: initial -> waitingForYouonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                reset();
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStageRef.current === 'waitingForYouonKey') {
            if (isYouonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                selectedYouonKeyRef.current = inputKey ?? null;
                internalStageRef.current = 'waitingForFirstDakuonKey';
                setCurrentTarget(''); // 1
                console.log('[Youhandakuon handleInput] Stage: waitingForYouonKey -> waitingForFirstDakuonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                internalStageRef.current = 'initial';
                setCurrentTarget(selectedGyouKeyRef.current); // 
                currentExpectedKeysRef.current = selectedGyouKeyRef.current ? [selectedGyouKeyRef.current] : [];
                console.log('[Youhandakuon handleInput] Canceled from waitingForYouonKey. Stage: initial. Target:', selectedGyouKeyRef.current);
                selectedYouonKeyRef.current = null;
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStageRef.current === 'waitingForFirstDakuonKey') {
            if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                selectedFirstDakuonKeyRef.current = inputKey ?? null;
                internalStageRef.current = 'waitingForSecondDakuonKey';
                setCurrentTarget(''); // 2
                console.log('[Youhandakuon handleInput] Stage: waitingForFirstDakuonKey -> waitingForSecondDakuonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                showHighlightForSecondDakuonRef.current = true; // 2
                setIsBlinking(true); // 
                if (blinkTimeoutRef.current !== null) clearTimeout(blinkTimeoutRef.current);
                blinkTimeoutRef.current = window.setTimeout(() => {
                    setIsBlinking(false);
                    blinkTimeoutRef.current = null;
                    //  getHighlightClassName  isBlinking  showHighlightForSecondDakuonRef 
                }, 300); // 0.3
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                internalStageRef.current = 'waitingForYouonKey';
                setCurrentTarget('');
                console.log('[Youhandakuon handleInput] Canceled from waitingForFirstDakuonKey. Stage: waitingForYouonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                selectedFirstDakuonKeyRef.current = null;
                showHighlightForSecondDakuonRef.current = false;
                setIsBlinking(false); // 
                if (blinkTimeoutRef.current !== null) {
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStageRef.current === 'waitingForSecondDakuonKey') {
            if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                selectedSecondDakuonKeyRef.current = inputKey ?? null;
                internalStageRef.current = 'waitingForDanKey';
                setCurrentTarget(inputDef.dan); // 
                console.log('[Youhandakuon handleInput] Stage: waitingForSecondDakuonKey -> waitingForDanKey. Target:', inputDef.dan);
                currentExpectedKeysRef.current = [inputDef.dan];
                setTargetLayerIndex(defaultTargetLayerIndex + 1); // 3
                showHighlightForSecondDakuonRef.current = false; // 
                setIsBlinking(false); //  ()
                return { isExpected: true, shouldGoToNext: false };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                internalStageRef.current = 'waitingForFirstDakuonKey';
                setCurrentTarget('');
                console.log('[Youhandakuon handleInput] Canceled from waitingForSecondDakuonKey. Stage: waitingForFirstDakuonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                selectedSecondDakuonKeyRef.current = null;
                setTargetLayerIndex(defaultTargetLayerIndex); // 2
                showHighlightForSecondDakuonRef.current = true; // 2
                setIsBlinking(false); // 
                if (blinkTimeoutRef.current !== null) {
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false };
            }
        } else if (internalStageRef.current === 'waitingForDanKey') {
            const danKeyIndex = danInputs.indexOf(inputKey ?? ''); // inputKeynull
            if (isDanKey(inputInfo.pressCode, layerForInputEvaluation, inputDef.dan)) { // 
                selectedDanKeyRef.current = inputKey ?? null;
                internalStageRef.current = 'stageCompleted';
                setStatus('correct');
                console.log('[Youhandakuon handleInput] Stage: waitingForDanKey -> stageCompleted. Target:', inputDef.dan, 'Char:', targetInfo.char);
                onAdvance?.(); // Call without arguments
                return { isExpected: true, shouldGoToNext: true };
            } else if (isCancelKey(inputInfo.pressCode, inputInfo.layerIndex)) {
                internalStageRef.current = 'waitingForSecondDakuonKey';
                setCurrentTarget('');
                console.log('[Youhandakuon handleInput] Canceled from waitingForDanKey. Stage: waitingForSecondDakuonKey. Target: ');
                currentExpectedKeysRef.current = [''];
                selectedDanKeyRef.current = null;
                setTargetLayerIndex(defaultTargetLayerIndex); // 2
                showHighlightForSecondDakuonRef.current = true; // 2
                setIsBlinking(false); // 
                if (blinkTimeoutRef.current !== null) {
                    clearTimeout(blinkTimeoutRef.current);
                    blinkTimeoutRef.current = null;
                }
                return { isExpected: true, shouldGoToNext: false }; // 
            } else if (isDakuonKey(inputInfo.pressCode, layerForInputEvaluation)) {
                // waitingForDanKey 
                //  (shouldGoToNext: false)
                return { isExpected: true, shouldGoToNext: false };
            }
            // 
        }

        // If none of the expected inputs for the current stage are met
        setStatus('incorrect');
        const errLayer = determineErrorLayer(layerForInputEvaluation); // 
        setErrorHighlightLayer(errLayer);
        console.log(`[Youhandakuon handleInput] Incorrect input. Stage: ${internalStageRef.current}, Target: ${currentTarget}, InputKey: ${inputKey}, ErrorLayer: ${errLayer}`);

        // ---  ---
        internalStageRef.current = 'initial';
        const currentTargetInfoForReset = getTargetInfo(); // dIdx  inputDef 
        if (currentTargetInfoForReset) {
            setCurrentTarget(currentTargetInfoForReset.inputDef.gyouKey);
            currentExpectedKeysRef.current = [currentTargetInfoForReset.inputDef.gyouKey];
        } else {
            //  ()
            setCurrentTarget(null);
            currentExpectedKeysRef.current = [];
        }
        selectedGyouKeyRef.current = null;
        selectedYouonKeyRef.current = null;
        selectedFirstDakuonKeyRef.current = null;
        selectedSecondDakuonKeyRef.current = null;
        selectedDanKeyRef.current = null;
        setTargetLayerIndex(defaultTargetLayerIndex); // 
        setTargetLayerIndices([defaultTargetLayerIndex, defaultTargetLayerIndex + 1]); // 
        showHighlightForSecondDakuonRef.current = false; // 2
        setIsBlinking(false); // 
        if (blinkTimeoutRef.current !== null) {
            clearTimeout(blinkTimeoutRef.current);
            blinkTimeoutRef.current = null;
        }
        // ---  ---

        return {
            isExpected: false,
            errorLayer: errLayer,
            shouldGoToNext: false, //  shouldGoToNext  false
        };
    }, [isActive, getTargetInfo, keymap, reset, onAdvance, targetLayerIndices]); // Added targetLayerIndices to dependencies

    const getHighlightClassName = useCallback((keyName: string, layer: number, keyIndex: number): PracticeHighlightResult => {
        const targetInfo = getTargetInfo(); // Recalculate targetInfo here

        // Log for every call to see parameters, especially for the "" key
        if (keyName === '' && layer === defaultTargetLayerIndex) {
            console.log(`[Youhandakuon getHighlightClassName for "" key] keyName: "${keyName}", layer: ${layer}, keyIndex: ${keyIndex}`);
            console.log(`  isActive: ${isActive}, targetInfo exists: ${!!targetInfo}, internalStage: ${internalStageRef.current}`);
            if (targetInfo) {
                console.log(`  inputDef.gyouKey: "${targetInfo.inputDef.gyouKey}", defaultTargetLayerIndex: ${defaultTargetLayerIndex}`);
            }
        }

        if (!isActive || !targetInfo) {
            // console.log(`[Youhandakuon getHighlightClassName] No highlight because !isActive (${!isActive}) or !targetInfo (${!targetInfo}) for key "${keyName}" on layer ${layer}`);
            return { className: null, overrideKey: null };
        }
        const { inputDef } = targetInfo;
        let isTarget = false;

        if (internalStageRef.current === 'initial' && keyName === inputDef.gyouKey && layer === defaultTargetLayerIndex) {
            isTarget = true;
            if (keyName === '') console.log('[Youhandakuon getHighlightClassName] "" key MATCHED initial stage highlight condition!');
        } else if (internalStageRef.current === 'waitingForYouonKey' && keyName === '' && layer === defaultTargetLayerIndex) {
            isTarget = true;
        } else if (internalStageRef.current === 'waitingForFirstDakuonKey' && keyName === '' && layer === defaultTargetLayerIndex) {
            isTarget = true;
        } else if (internalStageRef.current === 'waitingForSecondDakuonKey' && keyName === '' && layer === defaultTargetLayerIndex) {
            // 
            if (isBlinking) { // 
                return { className: null, overrideKey: null };
            }
            isTarget = showHighlightForSecondDakuonRef.current; // 2true
        } else if (internalStageRef.current === 'waitingForDanKey' && keyName === inputDef.dan && layer === defaultTargetLayerIndex + 1) {
            isTarget = true;
        }

        if (isTarget) {
            return { className: 'bg-blue-100 dark:bg-blue-800', overrideKey: null }; // Tailwind CSS 
        }

        return { className: null, overrideKey: null };
    }, [isActive, getTargetInfo, targetLayerIndex, isBlinking]); // isBlinking 

    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number): boolean => {
        if (!isActive || errorHighlightLayer === null) return false;
        // If an error highlight is active for the currentLayoutIndex,
        // and the currentKeyIndex matches the targetPressCode (the erroneously pressed key),
        // then this key is the one to be highlighted as an error.
        return currentLayoutIndex === errorHighlightLayer && currentKeyIndex === targetPressCode;
    }, [isActive, errorHighlightLayer]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setStatus('waiting'); //  waiting 
    }, [setErrorHighlightLayer, setStatus]);


    return {
        status,
        targetChar,
        currentTarget: currentTarget ?? undefined, // 
        headingChars,
        targetLayerIndex,
        targetLayerIndices,
        errorHighlightLayer,
        handleInput,
        getHighlightClassName,
        reset,
        isInvalidInputTarget, // Added missing property
        countdownValue: undefined, // 
        challengeTimer: undefined, // 
        challengeTotalTime: undefined, // 
        challengeResults: null, // 
        // typedKeysCount: 0, // This property is not in PracticeHookResult
        // mistypedKeysCount: 0, // This property is not in PracticeHookResult
        capitalizedCharIndices: new Set(), // 
        typedEndIndex: 0, // 
        clearErrorState, // Added clearErrorState
    };
};
