// /home/coffee/my-keymap-viewer/src/hooks/useTanbunChallengePractice.ts
import { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    PracticeHighlightResult,
    HighlightInfo,
    hid2Gyou,
    hid2Dan,
    functionKeyMaps,
    ChallengeResult,
    getHidKeyCodes, // 
    gyouChars,
    danList,
    youonGyouList,
    youonGyouChars,
    youonDanMapping,
    dakuonGyouChars,
    dakuonDanMapping,
    handakuonGyouChars,
    handakuonDanMapping,
    youdakuonGyouChars,
    youdakuonDanMapping,
    youhandakuonGyouChars,
    youhandakuonDanMapping,
    hid2Youon,
} from './usePracticeCommons';
import { tanbunSentences, kigoMapping3, kigoMapping2 } from '../data/keymapData'; //  kigoMapping3, kigoMapping2 

type ChallengeStatus = 'idle' | 'countdown' | 'running' | 'finished';

type TanbunProgressInfo = {
    typedEndIndex: number; // 
};


type InputState = {
    gyouKey: string | null; // 
    hasYouon: boolean;
    hasDakuon: boolean;
    hasHandakuon: boolean;
};
const initialInputState: InputState = { gyouKey: null, hasYouon: false, hasDakuon: false, hasHandakuon: false };


const COUNTDOWN_SECONDS = 5;
const INPUT_TIMEOUT_MS = 1500; // 1.5 ()
const TRAINING_DURATION_MS = 3 * 60 * 1000; // 3
const CHALLENGE_DURATION_SECONDS = 180; // 




const useTanbunChallengePractice = ({
    isActive,
    kb,
    side,
    layers, // layers 
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<ChallengeStatus>('idle');
    const [countdownValue, setCountdownValue] = useState<number>(COUNTDOWN_SECONDS);
    const [remainingTime, setRemainingTime] = useState<number>(TRAINING_DURATION_MS);
    const [currentSentence, setCurrentSentence] = useState<string>('');
    const [currentIndex, setCurrentIndex] = useState<number>(0); // 
    const [remainingSentences, setRemainingSentences] = useState<string[]>([]);
    const [inputState, setInputState] = useState<InputState>(initialInputState);
    const [progressInfo, setProgressInfo] = useState<TanbunProgressInfo>({ typedEndIndex: 0 });

    const countdownTimerRef = useRef<number | null>(null);
    const trainingTimerRef = useRef<number | null>(null);
    const startTimeRef = useRef<number | null>(null);
    const correctCountRef = useRef(0); // 
    const missCountRef = useRef(0); // 
    const totalCharsTypedRef = useRef(0); // 
    const completedCharsCountRef = useRef(0); // 
    const completedSentenceCountRef = useRef(0); // 
    const [challengeResults, setChallengeResults] = useState<ChallengeResult | null>(null);
    const inputTimeoutRef = useRef<number | null>(null);
    const prevIsActiveRef = useRef(isActive); // isActive 


    //  ( - useKanaChallengePractice )
    const funcKeyCodes = useMemo(() => {
        const map = functionKeyMaps[kb]?.[side] ?? {};
        const findCode = (name: string) => {
            const entry = Object.entries(map).find(([_, n]) => n === name);
            return entry ? parseInt(entry[0]) + 1 : null;
        };
        return {
            youon: findCode(''),
            dakuon: findCode(''),
            handakuon: findCode(''),
            sokuon: findCode(''),
            kigo: findCode(''), // 
        };
    }, [kb, side]);

    //  pressCode 
    // pressCode  0-based index
    const singleCharKeyCodes = useMemo(() => {
        const codes: Record<string, number | null> = {};
        // 
        const targetChars = ['', '', '', '', '', '', '', '', '', '', '', ''];

        // 0 () 
        const layer0 = layers[0];
        if (layer0) {
            targetChars.forEach(char => {
                const index = layer0.indexOf(char);
                if (index !== -1) {
                    codes[char] = index;
                }
            });
        }

        // 8 () 
        const layer8 = layers[8];
        if (layer8) {
            targetChars.forEach(char => {
                // \n
                const keyNameInLayout = char === '' ? '\n' : char;
                const index = layer8.indexOf(keyNameInLayout);
                if (index !== -1) {
                    codes[char] = index;
                }
            });
        }

        // 7 () 
        const layer7 = layers[7];
        if (layer7) {
             targetChars.forEach(char => {
                const index = layer7.indexOf(char);
                if (index !== -1) codes[char] = index;
            });
        }
        console.log("[TanbunChallenge singleCharKeyCodes]", codes);
        return codes;
    }, [kb, side, layers]); // layers 

    const clearInputTimeout = useCallback(() => {
        if (inputTimeoutRef.current !== null) {
            clearTimeout(inputTimeoutRef.current);
            inputTimeoutRef.current = null;
        }
    }, []);

    const resetInputStateAndClearTimeout = useCallback(() => {
        setInputState(initialInputState);
        clearInputTimeout();
    }, [clearInputTimeout]);

    // 
    const selectNextSentence = useCallback(() => {
        if (remainingSentences.length > 0) {
            const randomIndex = Math.floor(Math.random() * remainingSentences.length);
            const nextSentence = remainingSentences[randomIndex];
            setCurrentSentence(nextSentence);
            setCurrentIndex(0);
            setProgressInfo({ typedEndIndex: 0 });
            // 
            setRemainingSentences(prev => {
                const newRemaining = prev.filter((_, i) => i !== randomIndex);
                return newRemaining;
            });
        } else {
            setCurrentSentence("");
            setCurrentIndex(0); // currentIndex typedEndIndex 
            // setProgressInfo({ typedEndIndex: 0 }); // 
            // 
            // setStatus('finished');
        }
        clearInputTimeout();
    }, [remainingSentences, clearInputTimeout]);

    // 
    useEffect(() => {
        if (isActive && status === 'countdown') {
            if (countdownValue > 0) {
                countdownTimerRef.current = window.setTimeout(() => {
                    setCountdownValue(prev => prev - 1);
                }, 1000);
            } else {
                setStatus('running');
                setRemainingTime(TRAINING_DURATION_MS);
                startTimeRef.current = Date.now();
                setRemainingSentences([...tanbunSentences]);
            }
        }
        return () => {
            if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        };
    }, [isActive, status, countdownValue, selectNextSentence]);

    useEffect(() => {
        if (status === 'running' && remainingSentences.length > 0 && !currentSentence) {
            selectNextSentence();
        }
    }, [status, remainingSentences, currentSentence, selectNextSentence]);

    //  & 
    useEffect(() => {
        if (isActive && status === 'running') {
            trainingTimerRef.current = window.setInterval(() => {
                const elapsed = Date.now() - (startTimeRef.current ?? Date.now());
                const newRemaining = Math.max(0, TRAINING_DURATION_MS - elapsed);
                setRemainingTime(newRemaining);
                if (newRemaining === 0) {
                    setStatus('finished');
                    if (trainingTimerRef.current) clearInterval(trainingTimerRef.current);

                    const correct = completedCharsCountRef.current; // 
                    const miss = missCountRef.current;
                    const totalTyped = totalCharsTypedRef.current; // 
                    // : ( - ) / 
                    const accuracy = totalTyped > 0 ? Math.max(0, (totalTyped - miss) / totalTyped) : 0;
                    const durationMinutes = (TRAINING_DURATION_MS / 1000) / 60;
                    const cpmValue = durationMinutes > 0 ? Math.round(correct / durationMinutes) : 0;

                    const results: ChallengeResult = {
                        totalQuestions: completedSentenceCountRef.current, // 
                        totalCharsTyped: totalTyped, // 
                        correctCharsCount: correct, // 
                        correctCount: correct, // correctCount 
                        missCount: miss,
                        accuracy: accuracy,
                        score: 0, // Score will be calculated in PracticeView
                        cpm: cpmValue,
                        rankMessage: '', // Rank will be calculated in PracticeView
                    };
                    setChallengeResults(results);
                }
            }, 100);
        } else if (status === 'finished' && trainingTimerRef.current) {
             clearInterval(trainingTimerRef.current);
        }
        return () => {
            if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        };
    }, [isActive, status]);

    // 
    const reset = useCallback(() => {
        setStatus('idle');
        setCountdownValue(COUNTDOWN_SECONDS);
        setRemainingTime(TRAINING_DURATION_MS);
        setCurrentSentence('');
        setCurrentIndex(0);
        setRemainingSentences([]);
        setProgressInfo({ typedEndIndex: 0 });
        setChallengeResults(null);
        startTimeRef.current = null;
        setInputState(initialInputState);
        correctCountRef.current = 0; //  ()
        missCountRef.current = 0;
        totalCharsTypedRef.current = 0;
        completedCharsCountRef.current = 0; // 
        completedSentenceCountRef.current = 0;
        if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        clearInputTimeout();
    }, [clearInputTimeout]);

    // 
    useEffect(() => {
        // isActive  false 
        if (!isActive && prevIsActiveRef.current) {
            // console.log(`[TanbunChallenge useEffect] Resetting state because isActive became false.`);
            reset();
        } else if (isActive && status === 'idle') { // isActive  true  status  idle 
            setStatus('countdown');
        }
        prevIsActiveRef.current = isActive; // 
    }, [isActive, status, reset, clearInputTimeout]);

    // 
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        if (status !== 'running' || !currentSentence || inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false };
        }

        const pressCode = inputInfo.pressCode;
        totalCharsTypedRef.current += 1; // 

        let typedChar: string | null = null;
        let shouldReturnEarly = false;
        let isCorrect = false; // Flag for correctness

        // 1.  ()
        // pressCode
        const expectedChar = currentSentence[currentIndex];
        const expectedSingleCharPressCode = singleCharKeyCodes[expectedChar];
        if (expectedSingleCharPressCode !== undefined && expectedSingleCharPressCode !== null && pressCode === expectedSingleCharPressCode) {
             typedChar = expectedChar; // 
             setInputState(initialInputState); // 
             clearInputTimeout();
             console.log(`[TanbunChallenge handleInput] Single char match: '${typedChar}'`);
        } else {
        let charsToAdvance = 1; // Default advancement

        const expectedChar = currentSentence[currentIndex];
        const isLastChar = currentIndex === currentSentence.length - 1;

        const currentGyouKey = hid2Gyou(pressCode + 1, kb, side); // pressCode  0-based  +1
        const currentDanKey = hid2Dan(pressCode + 1, kb, side);   // pressCode  0-based  +1
        // const currentYouonKey = hid2Youon(pressCode + 1, kb, side); //  (pressCode + 1) - 
        const funcMap = functionKeyMaps[kb]?.[side] ?? {};
        const funcKeyName = Object.entries(funcMap).find(([idx, _]) => parseInt(idx) === pressCode)?.[1]; // pressCode  0-based

        console.log(`[TanbunChallenge handleInput] START. pressCode: 0x${pressCode.toString(16)}, currentGyouKey: ${currentGyouKey}, currentDanKey: ${currentDanKey}, funcKeyName: ${funcKeyName}, inputState:`, JSON.parse(JSON.stringify(inputState)));
        // 1.  ()
        if (funcKeyName === '') {
            typedChar = '';
            setInputState(initialInputState);
            // -> 
        } else if (funcKeyName === '') {
            if (inputState.gyouKey) {
                clearInputTimeout();
                setInputState(prev => ({ ...prev, hasYouon: true }));
                console.log(`[TanbunChallenge handleInput] Set hasYouon. New inputState:`, JSON.parse(JSON.stringify({ ...inputState, hasYouon: true })));
                inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                return { isExpected: true, shouldGoToNext: false }; // 
            } else {
                setInputState(initialInputState); // 
                clearInputTimeout();
                return { isExpected: false, shouldGoToNext: false };
            }
        } else if (funcKeyName === '') {
            clearInputTimeout(); // 
            if (inputState.gyouKey) {
                if (inputState.gyouKey === '' && inputState.hasDakuon && !inputState.hasHandakuon) { // 
                    //  +   -> 
                    setInputState(prev => ({ ...prev, hasDakuon: false, hasHandakuon: true }));
                    console.log(`[TanbunChallenge handleInput] Set hasHandakuon (from dakuon). New inputState:`, JSON.parse(JSON.stringify({ ...inputState, gyouKey: inputState.gyouKey, hasDakuon: false, hasHandakuon: true })));
                } else if (!inputState.hasDakuon && !inputState.hasHandakuon) { // 
                    //  ()
                    clearInputTimeout();
                    setInputState(prev => ({ ...prev, hasDakuon: true, hasHandakuon: false }));
                    console.log(`[TanbunChallenge handleInput] Set hasDakuon. New inputState:`, JSON.parse(JSON.stringify({ ...inputState, gyouKey: inputState.gyouKey, hasDakuon: true, hasHandakuon: false })));
                }
                inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                return { isExpected: true, shouldGoToNext: false }; // 
            } else {
                setInputState(initialInputState); // 
                return { isExpected: false, shouldGoToNext: false };
            }
        } else if (funcKeyName === '') {
            if (inputState.gyouKey === '') {
                clearInputTimeout();
                setInputState(prev => ({ ...prev, hasHandakuon: true, hasDakuon: false }));
                console.log(`[TanbunChallenge handleInput] Set hasHandakuon. New inputState:`, JSON.parse(JSON.stringify({ ...inputState, gyouKey: inputState.gyouKey, hasHandakuon: true, hasDakuon: false })));
                inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                return { isExpected: true, shouldGoToNext: false }; // 
            } else {
                setInputState(initialInputState); // 
                return { isExpected: false, shouldGoToNext: false };
            }
        } else if (funcKeyName === '') {
            clearInputTimeout();
            if (inputState.gyouKey) { //  (kigoPractice2)
                const gyou = inputState.gyouKey;
                // kigoPractice2  ( -> )
                if (kigoMapping2[gyou]) {
                    typedChar = kigoMapping2[gyou];
                }
                setInputState(initialInputState); // 
                // -> 
            } else { //  (kigoPractice3)
                setInputState({ ...initialInputState, gyouKey: '' }); //  '' 
                console.log(`[TanbunChallenge handleInput] Set gyouKey to ''. New inputState:`, JSON.parse(JSON.stringify({ ...initialInputState, gyouKey: '' })));
                inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                return { isExpected: true, shouldGoToNext: false }; // 
            }
        }
        // 3.  (inputState.gyouKey )
        else if (currentDanKey && inputState.gyouKey && inputState.gyouKey !== '') { // 
            clearInputTimeout();

            const gyou = inputState.gyouKey;
            const danIndex = danList.indexOf(currentDanKey);

            if (danIndex !== -1 && gyou !== '') { // 
                // ... () ...
                if (inputState.hasYouon && inputState.hasDakuon) { // 
                    const youdakuonDanIndex = youdakuonDanMapping[gyou]?.indexOf(currentDanKey);
                    if (youdakuonDanIndex !== -1) {
                        typedChar = youdakuonGyouChars[gyou]?.[youdakuonDanIndex] ?? null;                        
                        console.log(`[TanbunChallenge handleInput] Typed char (youdakuon): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, youdakuonDanIndex: ${youdakuonDanIndex}`);
                    }
                 }
                 else if (inputState.hasYouon && inputState.hasHandakuon) { //  (: )
                    const youhandakuonDanIndex = youhandakuonDanMapping[gyou]?.indexOf(currentDanKey);
                    if (youhandakuonDanIndex !== -1) {
                        typedChar = youhandakuonGyouChars[gyou]?.[youhandakuonDanIndex] ?? null;
                        console.log(`[TanbunChallenge handleInput] Typed char (youhandakuon): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, youhandakuonDanIndex: ${youhandakuonDanIndex}`);
                    }
                 }
                 else if (inputState.hasYouon) { //  (: )
                    const youonDanIndex = youonDanMapping[gyou]?.indexOf(currentDanKey);
                    if (youonDanIndex !== -1) {
                        typedChar = youonGyouChars[gyou]?.[youonDanIndex] ?? null;
                        console.log(`[TanbunChallenge handleInput] Typed char (youon): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, youonDanIndex: ${youonDanIndex}`);
                    }
                 }
                 else if (inputState.hasDakuon) { //  (: )
                    // 
                    if (dakuonDanMapping[gyou]?.includes(currentDanKey)) {
                         typedChar = dakuonGyouChars[gyou]?.[danIndex] ?? null;
                         console.log(`[TanbunChallenge handleInput] Typed char (dakuon): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, danIndex: ${danIndex}`);
                     }
                 }
                 else if (inputState.hasHandakuon) { //  (: )
                    //  (gyou  '' )
                    if (gyou === '' && handakuonDanMapping[gyou]?.includes(currentDanKey)) {
                        typedChar = handakuonGyouChars[gyou]?.[danIndex] ?? null;
                        console.log(`[TanbunChallenge handleInput] Typed char (handakuon): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, danIndex: ${danIndex}`);
                     }
                 }
                 else { //  (: )
                    if (gyou === '') {
                        if (currentDanKey === '') typedChar = gyouChars[gyou]?.[0] ?? null; // 
                        else if (currentDanKey === '') typedChar = gyouChars[gyou]?.[1] ?? null; // 
                        else if (currentDanKey === '') typedChar = gyouChars[gyou]?.[2] ?? null; // 
                    } else {
                        // 
                        typedChar = gyouChars[gyou]?.[danIndex] ?? null;
                    }
                    console.log(`[TanbunChallenge handleInput] Typed char (seion): ${typedChar}, gyou: ${gyou}, currentDanKey: ${currentDanKey}, danIndex: ${danIndex}`);
                 }
            }
            setInputState(initialInputState); // 
            // -> 
        }
        // 4. kigoPractice3 2 ()
        else if (inputState.gyouKey === '') {
            clearInputTimeout();
            // pressCode 0kigoMapping3 
            const kigoSymbol = kigoMapping3[pressCode];
            if (kigoSymbol) {
                typedChar = kigoSymbol.split('\n')[0];
            }
            setInputState(initialInputState);
            // -> 
        }
        // 5.  ()
        else if (currentGyouKey && !inputState.gyouKey && !inputState.hasYouon && !inputState.hasDakuon && !inputState.hasHandakuon) { // 
            clearInputTimeout();
            setInputState({ ...initialInputState, gyouKey: currentGyouKey });
            console.log(`[TanbunChallenge handleInput] Set gyouKey to '${currentGyouKey}'. New inputState:`, JSON.parse(JSON.stringify({ ...initialInputState, gyouKey: currentGyouKey })));
            inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
            return { isExpected: true, shouldGoToNext: false }; // 
        }
        //  (BS, ENT ) 
        else {
            clearInputTimeout();
            setInputState(initialInputState); // 
            return { isExpected: false, shouldGoToNext: false };
        }
        } // End of else block for single char check


        // 
        if (typedChar !== null) {
            console.log(`[TanbunChallenge handleInput] Character determined: typedChar='${typedChar}'`);
            let expectedCharsToCompare = "";
            let charsToAdvance = 1; // Default advancement
            if (currentIndex < currentSentence.length) { // 
                const firstChar = currentSentence[currentIndex];
                expectedCharsToCompare = firstChar; // 1
                charsToAdvance = 1;
                // 2/
                if (currentIndex + 1 < currentSentence.length) {
                    const secondChar = currentSentence[currentIndex + 1];
                    const smallYouonChars = ['', '', '', '', '', '', '', '']; // 
                    //  ()
                    const isBaseForYouon = ['', '', '', '', '', '', '', '', '', '', '', ''].includes(firstChar);
                    // 
                    // 
                    if (isBaseForYouon && smallYouonChars.includes(secondChar)) {
                        expectedCharsToCompare = firstChar + secondChar; // 2
                        charsToAdvance = 2;
                    }
                }
            }


            if (typedChar === expectedCharsToCompare) {
                isCorrect = true;
            }

            if (isCorrect) {
                // correctCountRef.current += charsToAdvance; // 
                completedCharsCountRef.current += charsToAdvance; // 
                const nextIndex = currentIndex + charsToAdvance;

                if (nextIndex >= currentSentence.length) {
                    //  ()
                    completedSentenceCountRef.current += 1;
                    selectNextSentence(); //  (currentIndex  selectNextSentence )
                } else {
                    setCurrentIndex(nextIndex);
                    setProgressInfo({ typedEndIndex: nextIndex });
                }
                return { isExpected: true, shouldGoToNext: false }; // shouldGoToNext  false
            } else {
                missCountRef.current += 1;
                // currentIndex 
                return { isExpected: false, shouldGoToNext: false };
            }
        } else if (typedChar === null) { // typedChar  null 
            console.warn(`[TanbunChallenge handleInput] Reached unexpected state where typedChar is null after processing.`);
            // 
            // 
            return { isExpected: false, shouldGoToNext: false };
        }
        //  return 
        return { isExpected: false, shouldGoToNext: false };
    }, [status, currentSentence, currentIndex, selectNextSentence, kb, side, funcKeyCodes, inputState,
        clearInputTimeout, resetInputStateAndClearTimeout, singleCharKeyCodes] // singleCharKeyCodes 
    );

    useEffect(() => { console.log(`[TanbunChallenge] currentIndex updated to: ${currentIndex}`); }, [currentIndex]);

    const getProgressInfo = useCallback((): TanbunProgressInfo => {
        return progressInfo;
    }, [progressInfo]);

    const practiceResult = useMemo(() => {
        // headingChars  useMemo 
        let calculatedHeadingChars: string[] = [];
        if (status === 'countdown') {
            calculatedHeadingChars = [`${countdownValue}`];
        } else if (status === 'finished') {
            calculatedHeadingChars = [''];
        } else if (status === 'running' && currentSentence) {
            calculatedHeadingChars = [currentSentence];
        }

        return {
            headingChars: calculatedHeadingChars, // 
            handleInput,
            getProgressInfo,
            reset,
            challengeResults,
            targetLayerIndex: null,
            displayLayers: [],
            getHighlightClassName: () => ({ className: null, overrideKey: null }),
            isInvalidInputTarget: () => false,
            typedEndIndex: progressInfo.typedEndIndex, //  typedEndIndex 
            status, // status 
            countdownValue, // countdownValue 
            challengeTimer: remainingTime,
            challengeTotalTime: TRAINING_DURATION_MS,
        };
    }, [
        status, countdownValue, currentSentence, // headingChars 
        handleInput,
        getProgressInfo,
        reset,
        challengeResults,
        progressInfo, // typedEndIndex 
        singleCharKeyCodes, // singleCharKeyCodes 
        remainingTime // remainingTime 
    ]);

    return practiceResult; // useMemo 
};

export default useTanbunChallengePractice;
